	content: "\e900";
}

.icon-building:before {
	content: "\e901";
}

.icon-fax:before {
	content: "\e902";
}

.icon-mail:before {
	content: "\e903";
}

.icon-menu:before {
	content: "\e904";
}

.icon-minus:before {
	content: "\e905";
}

