html {
	color: #454545;
	font-size: 100%;
	line-height: 1.4;
	font-family: "Open Sans", sans-serif;
	font-weight: 400;
}

body {
	font-size: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	letter-spacing: 3px;
	text-rendering: geometricPrecision;
	text-transform: uppercase;
	font-family: "Fira Sans", sans-serif;
	font-weight: 300;
}

h1 {
	font-size: 2rem;
}

h2 {
	font-size: 1.4rem;
}

h2 small {
	color: #1C8584;
	font-size: 70%;
	margin: 0 -0.2rem;
	font-weight: 300;
}

h2 span {
	display: block;
}

h2 span.small {
	font-size: 90%;
}

a {
	text-decoration: none;
	color: #1C8584;
}

.dark h1,
.dark h2,
.dark h3,
.dark h4,
.dark h5,
.dark h6,
.dark p,
.dark span {
	color: white;
}

.site-container {
	display: flex;
	flex-direction: column;
}

main.site-inner {
	display: flex;
	flex-direction: column;
}

footer {
	display: block;
}

footer .content {
	padding: 20px 1.25rem 60px;
	max-width: 100% !important;
}

section .content,
.section .content {
	padding: 20px 1.25rem;
}

.dark {
	overflow: hidden;
}

.dark .slant-outer {
	background: #1C8584;
	margin: 0 -100px;
}

.dark .slant-inner {
	background: #454545;
	height: 100%;
}

.dark .content-wrapper {
	margin: 0 100px;
	color: white;
	padding: 30px 0 60px;
}

.dark .content-wrapper .content {
	max-width: 600px;
	margin: 0 auto;
}

footer .logo-container {
	width: 100%;
	margin-bottom: 0.625rem;
	max-width: 20rem;
}

footer .text-container {
	flex: auto;
}

footer .footer-text {
	text-transform: uppercase;
	font-size: 0.75rem;
	text-align: center;
}

.main-logo .content {
	padding-left: 9%;
	padding-right: 9%;
	padding-bottom: 0;
	margin-bottom: -20px;
}

.landing-title .content-wrapper {
	padding: 30px 0;
}

.landing-title .content {
	padding: 5px 20px;
}

.landing-title h2 {
	text-align: center;
	letter-spacing: 0px;
	margin: 0;
	line-height: 1.2;
}

.icon {
	display: block;
}

.icon.arrow-down:before {
	content: url(../imgs/icons_arrow-down.svg);
}

.icon.mail:before {
	content: url(../imgs/icons_mail.svg);
}

.down .content {
	padding: 0;
}

.down .icon {
	width: 40px;
	height: auto;
	margin: -10px auto 0;
}

#about-us {
	background-image: url(../imgs/bg_people.svg);
	background-position: bottom;
	background-repeat: repeat-x;
	background-size: 120%;
}

#about-us .content {
	background-image: url(../imgs/bg_box.svg);
	background-position: -60px 40px;
	background-repeat: no-repeat;
	background-size: 70%;
}

#contact-us .contact {
	list-style: none;
	line-height: 2;
	padding: 0;
}

#contact-us .contact li .button {
	color: white;
	display: block;
	padding: 1.5rem 1rem;
	background-color: #1C8584;
	margin: 0 0 2rem;
}

#contact-us .contact li .button span {
	margin-right: 0.5rem;
	color: #454545;
}

#contact-us .contact li a.button {
	transition: 0.2s ease-in-out;
}

#contact-us .contact li a.button:hover {
	background-color: #25afae;
}

.playground {
	display: flex;
	flex-direction: column;
}

.playground #sec1,
.playground #sec2,
.playground #sec3 {
	min-height: 200px;
	padding: 1rem;
}

.playground .sec1,
.playground .sec2,
.playground .sec3 {
	min-height: 100px;
	margin: 1rem 0 0;
	flex-basis: 100%;
}

.playground #sec1 {
	display: flex;
	flex-direction: column;
	background: #111;
}

.playground #sec1 .sec1 {
	background: #444;
}

.playground #sec1 .sec2 {
	background: #555;
}

.playground #sec1 .sec3 {
	background: #666;
}

.playground #sec2 {
	background: #222;
}

.playground #sec3 {
	background: #333;
}

@media (min-width: 40em) {

body {
	font-size: 110%;
}

h1 {
	font-size: 2.5rem;
}

h2 {
	font-size: 2rem;
}

#about-us {
	background-size: 80%;
}

#about-us .content {
	background-image: none;
}

}

@media (min-width: 60em) {

body {
	font-size: 125%;
}

h1 {
	font-size: 3rem;
}

h2 {
	font-size: 2.25rem;
}

main.site-inner {
	flex-direction: row;
	flex-wrap: wrap;
}

footer .row {
	display: flex;
	flex-direction: row;
	align-items: center;
}

footer .logo-container {
	width: 20rem;
	margin-bottom: 0;
	margin-right: 5rem;
}

footer .footer-text {
	font-size: 1rem;
	color: #999;
	letter-spacing: 2px;
}

#landing {
	flex: 1 100%;
}

#about-us,
#contact-us {
	flex: 1 50%;
}

.main-logo .content {
	padding: 1rem 1rem 0 0;
	width: 30%;
	height: auto;
	margin-left: auto;
	margin-bottom: -8rem;
}

.landing-title h2 {
	letter-spacing: 3px;
	line-height: 1.4;
	margin: 1rem 0;
}

#about-us .content {
	background-image: none;
}

#about-us {
	background-image: none;
	background-image: url(../imgs/bg_box.svg);
	background-position: -30px 40px;
	background-repeat: no-repeat;
	background-size: 40%;
}

#about-us .content {
	max-width: 600px;
	margin: 0 auto;
}

main.site-inner {
	background-image: url(../imgs/bg_people_r.png);
	background-repeat: repeat-x;
	background-position: bottom;
	background-size: 50%;
}

#contact-us .contact li .button {
	padding: 0;
	background: none;
	color: #454545;
	font-size: 1.5rem;
}

#contact-us .contact li .button span {
	color: #1C8584;
}

.playground {
	flex-direction: row;
	flex-wrap: wrap;
}

.playground #sec1 {
	flex: 1 100%;
}

.playground #sec2,
.playground #sec3 {
	flex: 1;
}

}

